// import React, { Component } from "react";
import axios from "axios";
import { useEffect, useState } from "react";
import Slide from "react-reveal";

const Resume = (props)=> {
  const [educationState, setEducationState] = useState([]);
  const [experienceState, setExperienceState] = useState([]);

  const fetchData = async () => {
    const data1 = await axios.post('https://7v0kcp0cg7.execute-api.us-east-1.amazonaws.com/v1/degrees');
    const data2 = await axios.post('https://7v0kcp0cg7.execute-api.us-east-1.amazonaws.com/v1/experiences');
    setEducationState(data1['data']['Items']);
    setExperienceState(data2['data']['Items']);
    // console.log(data1['data']['Items']);
  }

  useEffect(() => {
    fetchData();
  }, []);

  const getRandomColor = ()=> {
    let letters = "0123456789ABCDEF";
    let color = "#";
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  
  if (!props.data) return null;
  const skillmessage = props.data.skillmessage;

  const education = educationState.map(function (education) {
    return (
      <div key={education.university.S}>
        <h3>{education.university.S}</h3>
        <p className="info">
          {education.degree.S} <span>&bull;</span>
          <em className="date">{education.date.S}</em>
        </p>
        <p>{education.courses.S}</p>
      </div>
    );
  });

  const work = experienceState.map(function (work) {
    return (
      <div key={work.company.S}>
        <h3>{work.company.S}</h3>
        <p className="info">
          {work.position.S}
          <span>&bull;</span> <em className="date">{work.period.S}</em>
        </p>
        <p>{work.description.S}</p>
      </div>
    );
  });

  const skills = props.data.skills.map((skills) => {
    const backgroundColor = getRandomColor();
    const className = "bar-expand " + skills.name.toLowerCase();
    const width = skills.level;

    return (
      <li key={skills.name}>
        <span style={{ width, backgroundColor }} className={className}></span>
        <em>{skills.name}</em>
      </li>
    );
  });

  return (
    <section id="resume">
      <Slide left duration={1300}>
        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{education}</div>
            </div>
          </div>
        </div>
      </Slide>

      <Slide left duration={1300}>
        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>

          <div className="nine columns main-col">{work}</div>
        </div>
      </Slide>

      <Slide left duration={1300}>
        <div className="row skill">
          <div className="three columns header-col">
            <h1>
              <span>Skills</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <p>{skillmessage}</p>

            <div className="bars">
              <ul className="skills">{skills}</ul>
            </div>
          </div>
        </div>
      </Slide>
    </section>
  );
  
}

export default Resume;
