import React, { useState } from "react";
import { Fade, Slide } from "react-reveal";
import axios from "axios";

const Contact = (props) =>  {
  const [nameState, setNameState] = useState('');
  const [bodyState, setBodyState] = useState('');
  const [emailState, setEmailState] = useState('');
  const [phoneState, setPhoneState] = useState('');
  const [errorState, setErrorState] = useState(false);
  const [subjectState, setSubjectState] = useState('');
  const [successState, setSuccessState] = useState(false);
  const [errorMessageState, setErrorMessageState] = useState('');

  if (!props.data) return null;

  const name = props.data.name;
  const street = props.data.address.street;
  const city = props.data.address.city;
  const state = props.data.address.state;
  const zip = props.data.address.zip;
  const phone = props.data.phone;
  const message = props.data.contactmessage;

  const formValidate = () => {
    if(nameState === '' || emailState === '' || bodyState === '') return false;
    return true;
  }

  const clearFields = () => {
    setBodyState('');
    setEmailState('');
    setNameState('');
    setPhoneState('');
    setSubjectState('');
  }

  const sendEmail = () => {
    if(!formValidate()) {
      setErrorState(true);
      setSuccessState(false);
      setErrorMessageState('Please fill all the required fields!');
      return;
    }
    
    const params = {
      Destination: {
        ToAddresses: ['bikram.samsohang@miu.edu']
      },
      Message: {
        Body: {
          Text: {
            Data: `Name: ${nameState}\nFrom: ${emailState}\n${phoneState !== '' ? ('Phone: ' + phoneState + '\n') : '\n'}\n ${bodyState}`
          }
          },
        Subject: {
          Data: subjectState
        }
      },
      Source: 'bikramleeemboo@gmail.com',
    };

    axios.post('https://7v0kcp0cg7.execute-api.us-east-1.amazonaws.com/v1/contact-me', params).then(res => {
      console.log('The email is sent: ', res);
      setSuccessState(true);
      setErrorState(false);
      clearFields();
    }).catch(err => {
      setErrorState(true);
      setSuccessState(false);
      setErrorMessageState('Error while sending email');
      console.error('Error while sending email: ', err);
    });
  }

  return (
    <section id="contact">
      <Fade bottom duration={1000}>
        <div className="row section-head">
          <div className="two columns header-col">
            <h1>
              <span>Get In Touch.</span>
            </h1>
          </div>

          <div className="ten columns">
            <p className="lead">{message}</p>
          </div>
        </div>
      </Fade>

      <div className="row">
        <Slide left duration={1000}>
          <div className="eight columns">
            <form name="contactForm">
              <fieldset>
                <div>
                  <label htmlFor="contactName">
                    Name <span className="required">*</span>
                  </label>
                  <input
                    type="text"
                    size="35"
                    id="contactName"
                    name="contactName"
                    onChange={(e) => setNameState(e.target.value)}
                    required
                    value={nameState}
                  />
                </div>

                <div>
                  <label htmlFor="contactEmail">
                    Email <span className="required">*</span>
                  </label>
                  <input
                    type="text"
                    size="35"
                    id="contactEmail"
                    name="contactEmail"
                    onChange={(e) => setEmailState(e.target.value)}
                    required
                    value={emailState}
                  />
                </div>

                <div>
                  <label htmlFor="subject">Subject</label>
                  <input
                    type="text"
                    size="35"
                    id="subject"
                    name="subject"
                    onChange={(e) => setSubjectState(e.target.value)}
                    value={subjectState}
                  />
                </div>

                <div>
                  <label htmlFor="contactSubject">Phone</label>
                  <input
                    type="text"
                    size="35"
                    id="contactSubject"
                    name="contactSubject"
                    onChange={(e) => setPhoneState(e.target.value)}
                    value={phoneState}
                  />
                </div>

                <div>
                  <label htmlFor="contactMessage">
                    Message <span className="required">*</span>
                  </label>
                  <textarea
                    cols="50"
                    rows="15"
                    id="contactMessage"
                    name="contactMessage"
                    onChange={(e) => setBodyState(e.target.value)}
                    required
                    value={bodyState}
                  ></textarea>
                </div>

                <div>
                  <button className="submit" onClick={sendEmail} type="button">Submit</button>
                  <span id="image-loader">
                    <img alt="" src="images/loader.gif" />
                  </span>
                </div>
              </fieldset>
            </form>

            {errorState && <div id="message-warning">{errorMessageState}</div>}
            {successState && (<div id="message-success">
              <i className="fa fa-check"></i>Your message was sent, thank you!
              <br />
            </div>)}
          </div>
        </Slide>

        <Slide right duration={1000}>
          <aside className="four columns footer-widgets">
            <div className="widget widget_contact">
              <h4>Address and Phone</h4>
              <p className="address">
                {name}
                <br />
                {street} <br />
                {city}, {state} {zip}
                <br />
                <span>{phone}</span>
              </p>
            </div>

            <div className="widget widget_tweets">
              <h4 className="widget-title">Blog</h4>
              <ul id="twitter">
                <li>
                  <span>
                    Using SES for sending emails in contact form.
                    <a target="_blank" href="/blog">Click here</a>
                  </span>
                  <b>
                    <a href="./">2 Days Ago</a>
                  </b>
                </li>
                {/* <li>
                  <span>
                    Sed ut perspiciatis unde omnis iste natus error sit
                    voluptatem accusantium doloremque laudantium, totam rem
                    aperiam, eaque ipsa quae ab illo inventore veritatis et
                    quasi
                    <a href="./">http://t.co/CGIrdxIlI3</a>
                  </span>
                  <b>
                    <a href="./">3 Days Ago</a>
                  </b>
                </li> */}
              </ul>
            </div>
          </aside>
        </Slide>
      </div>
    </section>
  );
  
}

export default Contact;
